import React from "react";
import { HoverArrow } from "../components/HoverArrow";

export const LinkButton = (props) => {
  return (
    <a
      className={`button ${props.inline && "button--inline"}`}
      href={props.href}
      target="_blank"
      rel="noopener noreferrer"
    >
      {props.text}
      <HoverArrow />
    </a>
  );
};
