import React, { useState, useEffect } from "react";
import { Switch, Route, useLocation } from "react-router-dom";

import { Home } from "./pages/Home";
import { Propcard } from "./pages/Propcard";
import { PropcardApp } from "./pages/PropcardApp";
import { Contact } from "./pages/Contact";

import { checkVisibleElements } from "./checkVisibility";

function App() {
  const [darkMode, updateDarkMode] = useState(true);

  // Ensures useEffect runs after page change where dark mode doesn't change
  useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
    checkVisibleElements();
    window.addEventListener(
      "scroll",
      function (event) {
        checkVisibleElements();
      },
      false
    );
  });

  const changeDarkMode = (bool) => {
    darkMode !== bool && updateDarkMode(bool);
  };

  return (
    <div className="App">
      <div className={`page-wrapper ${darkMode && "dark-mode"}`}>
        <Switch>
          <Route path="/foz/" exact>
            <Home darkModeOn={changeDarkMode} />
          </Route>
          <Route path="/foz/propcard" exact>
            <Propcard darkModeOn={changeDarkMode} />
          </Route>
          <Route path="/foz/propcard-app">
            <PropcardApp darkModeOn={changeDarkMode} />
          </Route>
          <Route path="/foz/contact">
            <Contact darkModeOn={changeDarkMode} />
          </Route>
        </Switch>
      </div>
    </div>
  );
}

export default App;
